:root {
  --background-grey: #eeeeee;
  --background-blue: #f4f9fe;
}

.page {
  height: 100%;
  padding: 15px;
}

.pageGrey {
  background-color: var(--background-grey);
}

.pageBlue {
  background-color: var(--background-blue);
}

.desc {
  color: #8f8f8f;
  padding: 0 0 6px 0;
  margin: 0 0 10px 0;
  border-bottom: 1px solid #b3b3b3;
}

.extraSpace {
  height: 200px;
}
